// --- THEME COLOR ---
$primary: #eb690b !default; //mlo by bt eb690b, je ea690b
$primary-mix-percentage: 1.875% !default;
$primary-adjust-deg: -2.8deg !default;

// COLORS
$color-01:  #e95931 !default;
$color-02:  #b33131 !default;
$color-03:  #e72582 !default;
$color-04:  #009ed5 !default;
$color-05:  #913e8c !default;
$color-06:  #005087 !default;
$color-07:  #ffba00 !default;

// --- BACKGROUND NET ---
$bg-net-background: $primary;

$bg-net:     '../img/bg/net-03.png' !default;
$bg-net-inv: '../img/bg/net-inv-03.png' !default;
$bg-net-inv-top: '../img/bg/net-inv-top-03.png' !default;
$bg-net-inv-bottom: '../img/bg/net-inv-bottom-03.png' !default;

$bg-net-svg:     '../img/bg/net-03.svg' !default;
$bg-net-inv-svg: '../img/bg/net-inv-03.svg' !default;
$bg-net-inv-top-svg: '../img/bg/net-inv-top-03.svg' !default;
$bg-net-inv-bottom-svg: '../img/bg/net-inv-bottom-03.svg' !default;

$bg-net-inv-color-01-top: '../img/bg/net-inv-top-color-01.png' !default;
$bg-net-inv-color-01-bottom: '../img/bg/net-inv-bottom-color-01.png' !default;
$bg-net-inv-color-08-top: '../img/bg/net-inv-top-color-08.png' !default;
$bg-net-inv-color-08-bottom: '../img/bg/net-inv-bottom-color-08.png' !default;

// --- BACKGROUND DASHED ---
$bg-dashed: url("../img/bg/dashed-03.png") !default;

// --- STYLES ---
@import "styles.scss";

// BG FOOTER
.bg-footer { background-color: darken($primary, 17%) !important; }
.bg-footer-dark { background-color: darken($primary, 22%) !important; }
.bg-footer, .bg-footer-dark {
  .text-primary {
    color: lighten($primary, 12%) !important;
  }
}